<template>
  <div class="limit asd">
    <div class="details">
      <div class="items">
        <span v-for="(item, index) in str" :key="index" :class="{ areaItem: true, btns: en == item.val }" @click="getEn(item.val)">
          {{ item.name }}
        </span>
      </div>
      <div class="items">
        <span v-for="(item, index) in type" :key="index" :class="{ areaItem: true, btns: lang == item.val }" @click="getLang(item.val)">
          {{ item.name }}
        </span>
      </div>
      <div class="items">
        <span v-for="(item, index) in area" :key="index" :class="{ areaItem: true, btns: classify == item.val }" @click="getClassify(item.val)">
          {{ item.name }}
        </span>
      </div>
    </div>

    <load-more @scroll-state="load">
      <!-- <div class="allItem"> -->
      <singers :singer="singers" :loadStatus="loadStatus"></singers>
      <!-- </div> -->
    </load-more>
    <loading v-show="isLoading"></loading>
  </div>
</template>

<script>
import singers from '@/components/common/singer/singer'
import loadMore from '@/components/common/loadMore/loadMore'

import { singer } from '@/network/singer'
export default {
  components: {
    singers,
    loadMore
  },
  data() {
    return {
      lang: -1,
      en: 0,
      classify: -1,
      singers: [],
      more: null,
      params: {
        limit: 35,
        offset: 0,
        type: -1,
        area: -1,
        initial: -1
      },
      isLoading: true,

      area: [
        { val: -1, name: '全部' },
        { val: 7, name: '华语' },
        { val: 96, name: '欧美' },
        { val: 8, name: '日本' },
        { val: 16, name: '韩国' },
        { val: 0, name: '其他' }
      ],
      type: [
        { val: -1, name: '全部' },
        { val: 1, name: '男歌手' },
        { val: 2, name: '女歌手' },
        { val: 3, name: '乐队' }
      ],
      loadStatus: false,

      str: [
        { val: 0, name: '全部' },
        { val: 1, name: 'A' },
        { val: 2, name: 'B' },
        { val: 3, name: 'C' },
        { val: 4, name: 'D' },
        { val: 5, name: 'E' },
        { val: 6, name: 'F' },
        { val: 7, name: 'G' },
        { val: 8, name: 'H' },
        { val: 9, name: 'I' },
        { val: 10, name: 'J' },
        { val: 11, name: 'K' },
        { val: 12, name: 'L' },
        { val: 13, name: 'M' },
        { val: 14, name: 'N' },
        { val: 15, name: 'O' },
        { val: 16, name: 'P' },
        { val: 17, name: 'Q' },
        { val: 18, name: 'R' },
        { val: 19, name: 'S' },
        { val: 20, name: 'T' },
        { val: 21, name: 'U' },
        { val: 22, name: 'V' },
        { val: 23, name: 'W' },
        { val: 24, name: 'X' },
        { val: 25, name: 'Y' },
        { val: 26, name: 'Z' },
        { val: 27, name: '#' }
      ]
    }
  },
  created() {
    this.getSinger()
  },
  computed: {},
  methods: {
    async getSinger() {
      let res = await singer(this.params)
      console.log(res);
          this.loadStatus = false

      this.singers = this.singers.concat( res.data.artists)
      if (res.data.more) {
       
        this.params.offset += this.params.limit
        this.isLoading = false
         this.loadStatus = true
      } else{
          this.loadStatus = false

      }

    },
    getEn(index) {
      this.en = index
      if (this.str[index].name === '全部') {
        this.params.initial = 0
        this.getSinger()
        return
      }
      this.params.initial = this.str[index].name
      this.getSinger()
    },
    getLang(index) {
      this.lang = index
      this.params.type = index
      this.getSinger()
    },
    getClassify(index) {
      this.classify = index
      this.params.area = index
      console.log(index)
      this.getSinger()
    },
    // 加载更多
    load() {
      if (this.loadStatus) {
        setTimeout(() => {
          this.getSinger()
          this.loadStatus = false
        }, 1000)
      }
    }
  }
}
</script>

<style scoped lang="less">
.asd {
  position: relative;
}
.details {
  padding: 30px 0;
  .items {
    margin: 20px 0;
  }
  .areaItem {
    width: 30px;
    height: 28px;
    padding: 5px;
    line-height: 28px;
    margin-right: 15px;
    text-align: center;
    font-size: 13px;
    color: #333333;
    cursor: pointer;
    border-radius: 3px;
  }

  .btns {
    background-color: pink;
    color: #fff;
  }
}

.allItem {
  padding: 0 20px;
  margin-bottom: 15px;
}
</style>
